import React, { Component, Fragment } from 'react';
// component
import Title from '../../title/Title'
import ListPortfolio from './ListPortfolio'
import LoadingData from '../../loading/LoadingData'
// library reactstrap
import { Jumbotron, Container, } from 'reactstrap'
import Grid from '@material-ui/core/Grid';
import axios from 'axios';


class Portfolio extends Component {
    constructor(props) {
        super(props)

        this.state = { 
            portfolio: [],
            isLoading:true,
         }
    }

     componentDidMount() {
         setTimeout(() => {
            axios.get('https://personal.adinata.site/api/portfolio')
            .then((result) => {
                // console.log(result.data.data)
               this.setState({ 
                   portfolio: result.data.data,
                   isLoading:false
               })
            })
        }, 300);
        
     }
    render() { 
        const { isLoading, portfolio} = this.state
        let linkImg = 'https://personal.adinata.site/images/' 
        if (isLoading) {
            return (
                <LoadingData />
            )
        }
        return ( 
            <Fragment>
                <Jumbotron fluid style={{background: 'transparent', boxShadow: 'none', minHeight:600}}>
                    <Container fluid>
                        <Container>
                            <Title title="Portfolio" />
                            <div style={{backgroundColor:'white', width:'50%', height:4}}></div>
                            <Grid container spacing={3} >
                            {
                                 portfolio.map(data => {
                                    return (
                                    <Grid item xs={12} sm={12} key={data.id}>
                                        <ListPortfolio  title={data.title} description={data.description} img={linkImg+data.images} />
                                    </Grid> )
                                })
                            }
                            </Grid>
                        </Container>
                    </Container>
                </Jumbotron>
            </Fragment>
         );
    }
}
 
export default Portfolio;