import React, { Component, Fragment } from 'react';
import { Jumbotron, Container } from 'reactstrap';
import axios from 'axios';
// component
import LoadingData from '../../loading/LoadingData'
class About extends Component {
    state = { 
        about: [],
        isLoading:true,
     }

     componentDidMount() {
        setTimeout(() => {
            axios.get('https://personal.adinata.site/api/config')
            .then((res) => {
                // console.log(res.data.data)
                this.setState({
                    about: res.data.data,
                    isLoading:false
                })
            })
        }, 300);
     }
    render() { 
        const {isLoading, about} = this.state
        if (isLoading) {
            return <LoadingData />
        }
        return ( 
            <Fragment>
                <Jumbotron fluid style={{background: 'transparent', boxShadow: 'none', minHeight:600}}>
                    <Container fluid>
                        <Container>
                            <h1 style={{color:'white'}} className="display-3">About Me</h1>
                            <div style={{backgroundColor:'white', width:'50%', height:4}}></div><br/>
                            {
                                about.map(data => {
                                    return <p key={data.id} style={{color:'white', maxWidth:600, textAlign:'justify'}} className="lead">{data.about}</p>
                                })
                            }

                        </Container>
                    </Container>
                </Jumbotron>
            </Fragment>
         );
    }
}
 
export default About;