import React from 'react';
import {BrowserRouter, Switch, Route, Link} from 'react-router-dom';
import {
  Collapse,
  Navbar, 
  Nav,
  NavItem,
  NavLink, 
    } from 'reactstrap';
// pages
import Intro from '../pages/Intro'
import About from '../pages/about/About'
import Portfolio from '../pages/portfolio/Portfolio'

function NoMatch () {
  return(
    <div>
      <h1 style={{color:'white'}}>Sorry the page you are looking, is not found.</h1>
    </div>
  )
}

class Header extends React.Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      isOpen: false
    };
  }
  toggle() {
    this.setState({
      isOpen: !this.state.isOpen
    });
  }
  
 

  render() {
    return (
      <BrowserRouter>
        <Navbar style={{  background: 'transparent', boxShadow: 'none',  padding:15,}} dark  expand="md">
          {/* <NavbarToggler style={{background: 'transparent', boxShadow: 'none', borderRadius:'50%'}}  /> */}
            <div style={{marginLeft:'5%', cursor:'pointer'}}  onClick={this.toggle}>
              <div style={{backgroundColor:'white', width:27, height:3.2, margin:6}}></div>
              <div style={{backgroundColor:'white', width:43, height:3.5, margin:6}}></div>
              <div style={{backgroundColor:'white', width:34.5, height:3.5, margin:6}}></div>
            </div>
          <Collapse isOpen={this.state.isOpen} navbar style={{marginLeft:'4%'}}>
            <Nav className="ml-auto" navbar style={{marginRight:'30%'}}>
              <NavItem>
                <NavLink style={{fontSize:20, color:'white', fontFamily:'serif', marginRight:55}} tag={Link} to="/" >Home</NavLink>
              </NavItem>
              <NavItem>
                <NavLink style={{fontSize:20, color:'white', fontFamily:'serif', marginRight:55}} tag={Link} to="/about" >About</NavLink>
              </NavItem>
              <NavItem>
                <NavLink style={{fontSize:20, color:'white', fontFamily:'serif', marginRight:55}} tag={Link} to="/portfolio">Portfolio</NavLink>
              </NavItem>
              <NavItem>
                <NavLink style={{fontSize:20, color:'white', fontFamily:'serif', marginRight:55}} tag={Link} to="#">Article</NavLink>
              </NavItem>
            </Nav>
          </Collapse>
        </Navbar>
        {/* <center style={{marginTop: '20%',}}>
          <Spinner style={{ width: '7rem', height: '7rem', color:'#16a085' }} type="grow" />
        </center> */}
        <Switch>
          <Route path='/' exact component={Intro} />
          <Route path='/about' exact component={About} />
          <Route path='/portfolio' exact component={Portfolio} />
          <Route component={NoMatch} />
        </Switch>
      </BrowserRouter>
    );
  }
}


export default Header;