import React, { Component, Fragment } from 'react';
import { Jumbotron,Container } from 'reactstrap';
import { SocialIcon } from 'react-social-icons';
import Axios from 'axios';
import LoadingData from '../loading/LoadingData';

class Intro extends Component {
    state = { 
        slogan: [],
        isLoading: true,
     }

     componentDidMount() {
        setTimeout(() => {
            Axios.get('https://personal.adinata.site/api/config')
            .then( (res) => {
                // console.log(res)
                this.setState({
                    slogan:res.data.data,
                    isLoading:false
                })
            })
        }, 300);
     }
    render() { 
        const {slogan, isLoading} = this.state
        if (isLoading) {
            return <LoadingData />
        }
        return ( 
            <Fragment>
                <Jumbotron style={{background: 'transparent', boxShadow: 'none', minHeight:600}}>
                    <Container style={{maxWidth:680}}>
                        <h1 className="display-3" style={{color:'white', fontFamily:'arial'}}>Hi, I am <br/> Alfy Adinata</h1>
                        <hr className="my-2" style={{backgroundColor:'white'}} />
                        {
                            slogan.map( data => {
                                return <p className="lead" style={{color:'#dfdfdf', textAlign:'center'}} key={data.id}>{data.slogan}</p>
                            })
                        }
                        <hr className="my-2" style={{backgroundColor:'white'}} />
                            <SocialIcon style={{margin: 10,}} url="http://github.com/alfyadinata" target="_blank" />
                            <SocialIcon style={{margin: 10,}} url="http://linkedin.com/in/alfy-adinata-886462189/" target="_blank" />
                            <SocialIcon style={{margin: 10,}} url="http://t.me/alfyadinata" target="_blank" />
                    </Container>
                </Jumbotron>
            </Fragment>
         );
    }
}
 
export default Intro;